.TeamPage {
	display: grid;
	grid-template-rows: 1fr 1fr 1fr;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	grid-column-gap: 10px;
	grid-row-gap: 10px;
	
	.team-name-section {
		grid-column: span 3;
		justify-self: start;
		align-self: center;
	}
	
	.team-name {
		font-size: 4rem;
		margin-bottom: 10px;
	}
	
	.win-loss-section {
		justify-self: center;
		padding: 30px;
		text-align: center;
	}
	
	 .match-detail-section{
		grid-column: span 4;
	}
	
}

