@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
* {
	box-sizing: border-box;
	padding: 0px;
	margin: 1px;
	background-color: #FFFFE6;
}

a {
	text-shadow: gray;
}
body {
  margin: 0;
  font-family: 'Ubuntu', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 1100px;
  margin: 0 auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:left;padding:20px}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:10px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}.MatchDetailCard .won-card{background-color:#4da375}.MatchDetailCard .loss-card{background-color:#a34d5d}

.MatchSmallCard{padding:10px;margin-bottom:10px}.MatchSmallCard .match-date,.MatchSmallCard .match-venue,.MatchSmallCard .match-result{margin-top:10px}.MatchSmallCard .additional-detail{text-align:right}.MatchSmallCard .additional-detail>p{margin-bottom:20px}.MatchSmallCard .won-card{background-color:#4da375}.MatchSmallCard .loss-card{background-color:#a34d5d}

.TeamPage{display:grid;grid-template-rows:1fr 1fr 1fr;grid-template-columns:1fr 1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:center}.TeamPage .team-name{font-size:4rem;margin-bottom:10px}.TeamPage .win-loss-section{justify-self:center;padding:30px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;border-style:dotted}.MatchPage .year-selector{justify-self:left}.MatchPage .match-detail{justify-style:end}

.TeamTile{height:50px;text-align:center;border-style:ridge}

.HomeTeamPage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}

